<template>
  <div :class="className" :id="id" :style="{height:height,width:width}"></div>
</template>
<script>
// 
let echarts = require('echarts/lib/echarts')
// 
require('echarts/lib/chart/bar')
// title
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
require('echarts/lib/component/axisPointer')
export default {
  name: 'sl-bar',
  props: {
    className: {
      type: String,
      default: 'echart'
    },
    id: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '200px'
    },
    height: {
      type: String,
      default: '280px'
    },
    orginData: {
      type: Array,
      default: _ => []
    },
    chartSetting: {
      type: Object,
      default: _ => {}
    }
  },
  data () {
    return {
      chart: null
    }
  },
  mounted () {
    window.addEventListener('resize', function () {
      this.chart.resize()
    })
  },
  beforeDestroy () {
    if (!this.chart) {
      return
    }
    this.chart.dispose()
    this.chart = null
  },
  watch: {
    orginData () {
      this.initChart()
    }
  },
  methods: {
    initChart () {
      this.chart = echarts.init(document.getElementById(this.id))
      let option = {
        color: ['#3398DB'],
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 
            type: 'shadow' // 'line' | 'shadow'
          }
        },
        grid: {
          left: '1%',
          right: '4%',
          bottom: '3%',
          containLabel: true

        },
        xAxis: [
          {
            type: 'category',
            data: [],
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            splitLine: {
              lineStyle: {
                type: 'dashed'
              }
            }
          }
        ],
        series: [
          {
            name: '',
            type: 'bar',
            barWidth: '50%',
            data: this.orginData
          }
        ]
      }
      const opt = Object.assign({}, option, this.chartSetting)
      this.chart.setOption(opt)
    }
  }

}
</script>


