<template>
  <div :class="className" :id="id" :style="{height:height,width:width}"></div>
</template>
<script>
// 
let echarts = require('echarts/lib/echarts')
// 
require('echarts/lib/chart/line')
// title
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
export default {
  name: 'sl-line',
  props: {
    className: {
      type: String,
      default: 'echart'
    },
    id: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '200px'
    },
    height: {
      type: String,
      default: '200px'
    },
    orginData: {
      type: Object,
      default: _ => {}
    }
  },
  data () {
    return {
      chart: null
    }
  },
  mounted () {
    window.addEventListener('resize', function () {
      this.chart.resize()
    })
  },
  beforeDestroy () {
    if (!this.chart) {
      return
    }
    this.chart.dispose()
    this.chart = null
  },
  watch: {
    orginData () {
      this.initChart()
    }
  },
  methods: {
    initChart () {
      this.chart = echarts.init(document.getElementById(this.id))
      let option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'line',
            label: {
              backgroundColor: '#ffffff'
            }
          }
        },
        legend: {
          data: ['', '']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: false,
            data: this.orginData.days
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: [
          {
            name: '',
            type: 'line',
            stack: '',
            symbolSize: 8,
            areaStyle: {
              normal: {
                color: 'rgba(6,179,216,.6)'
              }
            },
            data: this.orginData.dayParkingCount
          },
          {
            name: '',
            type: 'line',
            stack: '',
            symbolSize: 8,
            areaStyle: {
              normal: {
                color: 'rgba(234,162,70,.6)'
              }
            },
            data: this.orginData.daytradeCount
          }
        ],
        lineStyle: {
          normal: {
            shadowColor: 'rgba(225,225,225,0.6)',
            shadowBlur: 3,
            shadowOffsetY: 0,
            shadowOffsetX: 0,
            opacity: 1
          }
        },
        color: ['#06b3d8', '#eaa246']

      }
      this.chart.setOption(option)
    }
  }

}
</script>


