<style lang="less" scoped>
@import "../../assets/css/grad/grad.css";
@import "./home.css";
</style>
<template>
  <div class="home container-fluid">
    <!--  -->    
    <div class="row" id="todayData">
      <div class="col-md-3 col-sm-6 col-xs-12">
        <div class="lab">
          <div class="container-fluid">
            <div class="row mar-t20 colora3 clearfix">
              <div class="col-xs-10 text-left">
                
              </div>
              <div class="col-xs-2  text-right">
                <i class="iconfont" title="">&#xe606;</i>
              </div>
            </div>
            <div class="row income text-center enbleclick">
              <span></span>
              <span :title="orderData.todayorderIncome">{{orderData.todayorderIncome | toThousandslsFilter}}</span>
            </div>
            <div class="row chart mar0">
              <!--  -->
              <histogram style="height:'44px';width:'100%'" :orginData = "orderData.chartData"></histogram>
            </div>
            <div class="row avrage mar0">
              
              <span class="money enbleclick">{{orderData.monthorderIncome | toThousandslsFilter}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-3 col-sm-6 col-xs-12">
        <div class="lab">
          <div class="container-fluid">
            <div class="row mar-t20 colora3 clearfix">
              <div class="col-xs-10 text-left">
                
              </div>
              <div class="col-xs-2 text-right">
                <i class="iconfont" title="">&#xe606;</i>

              </div>
            </div>
            <div class="row income text-center enbleclick">
              <span :title="tradeData.todaytradeIncome">{{tradeData.todaytradeIncome | toThousandslsFilter}}</span>
            </div>
            <div class="row mar0 chart">
              <!--  -->
             <line-Chart id = "getTodayTradeEchart" style="height:'44px';width:'100%'" :orginData = "tradeData.chartData"></line-Chart>
            </div>
            <div class="row avrage mar0">
              
              <span class="money enbleclick">{{tradeData.monthtradeIncome | toThousandslsFilter}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-3 col-sm-6 col-xs-12">
        <div class="lab">
          <div class="container-fluid">
            <div class="row mar-t20 colora3 clearfix">
              <div class="col-xs-10 text-left">
                
              </div>
              <div class="col-xs-2 text-right">
                <i class="iconfont" title="APP">&#xe606;</i>
              </div>
            </div>
            <div class="row income text-center enbleclick" data-bind="click:checkTodayCharge">
              <span></span>
              <span :title="chargeData.todaychargeIncome">{{chargeData.todaychargeIncome | toThousandslsFilter}}</span>
            </div>
            <div class="row mar0 chart">
             <line-Chart id = "getchargeEchart" style="height:'44px';width:'100%'" :orginData = "chargeData.chartData"></line-Chart>
              
            </div>
            <div class="row avrage mar0">
              
              <span class="money enbleclick">{{chargeData.monthchargeIncome | toThousandslsFilter}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-3 col-sm-6 col-xs-12">
        <div class="lab">
          <div class="container-fluid">
            <div class="row mar-t20 colora3 clearfix">
              <div class="col-xs-10 text-left">
                
              </div>
              <div class="col-xs-2 text-right">
                <i class="iconfont" title="">&#xe606;</i>
              </div>
            </div>
            <div class="row income text-center enbleclick">
              <span :title="trafficData.todayTransportAttract">{{trafficData.todayTransportAttract | toThousandslsFilter}}</span>
            </div>
            <div class="row chart mar0">
              <histogram id= "getAttractionEchart" style="height:'44px';width:'100%'" :orginData = "trafficData.chartData"></histogram>
              
            </div>
            <div class="row avrage mar0">
              <span>
                <span class="money">{{trafficData.spaceCount | toThousandslsFilter }}</span>
              </span>
              <span class="fr">
                <span class="money">{{trafficData.parklotCount | toThousandslsFilter}}</span>
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <!--  -->
     <div class="row two" id="tradeData">
            <div class="col-md-12 charge-title">
                <h3></h3>
            </div>
            <div class="col-md-8 col-xs-12 ">
                <h4 class="ml34"></h4>
                <div>
                    <charge-Amount id = "getMounthTradeEchart" style="width:'100%';height:280px" :orginData="monthTradeChartData" :chartSetting="monthTradeSetting"></charge-Amount>
                </div>
            </div>
            <div class="col-md-4 col-xs-12">
                <h4></h4>
                <div class="parklot_income_range">
                    <ul>
                        <li class="clearfix parklot_income_item mar-t20 enbleclick" data-bind="click:$parent.checkParklotRange" v-for="(val,key) in getParklotIncomeRank" :key="key">
                            <span class="range_front" :class=" { gray: key > 2 }">{{key+1}}</span>
                            <span class="parklot_name">{{val.parklotAddress+'/'+val.parklotName}}</span>
                            <span class="parklot_income fr" ><span>{{val.parklotOrderCount | toThousandslsFilter}}</span>
                                &nbsp;<span>{{val.parklotIncome | toThousandslsFilter}}</span></span>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
      <!--  -->
      <!--  -->
        <div class="row " id="userData" style="margin:0;">
            <div class="col-md-6 col-xs-12">
                <div class="row module1 clearfix">
                    <div class="col-md-12 charge-title">
                        <h3></h3>
                    </div>
                    <div class="col-md-6 mb50 register-item">
                        <div class="row perimg">
                                <div class="col-xs-12 title">
                                        <div style="width:61%;">
                                                
                                            </div>
                                  </div>
                            <div class="col-xs-12">
                                <img  src="@/assets/img/home/1.png" alt="" title="">
                                <span class="num ellipsis" :title="register.userRegisterAmount">{{register.userRegisterAmount | toThousandslsFilter}}</span>
                                
                            </div>
                        </div>
                    </div>
                    <div class="col-md-6 mb50 register-item">
                        <div class=" row perimg">
                                <div class="col-xs-12 title">
                                        <div style="width:61%"></div>
                                    </div>
                            <div class="col-xs-12">
                                <img  src="@/assets/img/home/3.png" alt="" title="">
                                <span class="num ellipsis" :title="register.monthRegisterAmount">{{register.monthRegisterAmount | toThousandslsFilter}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-6 register-item">
                        <div class="row perimg">
                                <div class="col-xs-12 title">
                                        <div style="width:61%"></div>
                                    </div>
                            <div class="col-xs-12">
                                <img  src="@/assets/img/home/2.png" alt="" title="">
                                <span class="num ellipsis" :title="register.todayRegisterAmount">{{register.todayRegisterAmount | toThousandslsFilter}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-6 register-item">
                        <div class="row perimg">
                                <div class="col-xs-12 title">
                                        <div style="width:61%"></div>
                                    </div>
                            <div class="col-xs-12">
                                <img src="@/assets/img/home/4.png" alt="" title="">
                                <span class="num ellipsis" :title="register.monthInviteAmount">{{register.monthInviteAmount | toThousandslsFilter}}</span>

                            </div>
                        </div>
                    </div>
                </div>
                <div class="row module3 clearfix">
                    <div class="col-md-6 charge-title">
                        <h3></h3>
                    </div>
                    <div class="col-md-6 charge-title">
                        <h3></h3>
                    </div>
                    <div class="col-md-6">
                            <img src="@/assets/img/home/parking.png" alt="" title="">
                            <span class="ver-b num">{{mpActiveAmount | toThousandslsFilter}}</span>
                    </div>
                    <div class="col-md-6">
                            <img src="@/assets/img/home/parking.png" alt="" title="">
                            <span class="ver-b num">{{ypActiveAmount | toThousandslsFilter}}</span>
                    </div>
                </div>
            </div>
            <div class="col-md-6 col-xs-12">
                <div class="row module2 clearfix">
                    <div class="col-md-12 charge-title">
                        <h3></h3>
                    </div>
                    <div class="col-md-12">
                      <!--  -->
                        <div style="width:100%;height:294px;">
                          <tranalysic id ="getChargeTradeEchart" style="width:100%;height:294px;" :orginData = "chargeTradeChart" :chartSetting="chargeTradeSetting"></tranalysic>
                        </div>
                    </div>
                </div>
                <div class="row module4 clearfix">
                    <div class="col-md-12 charge-title">
                        <h3></h3>
                    </div>
                    <div class="col-md-12">
                        <line-Area id = "tradeOrderEchart" style="width:100%;height:292px;" :orginData="tradeOrderData" ></line-Area>
                    </div>
                </div>
            </div>
        </div>
  </div>
</template>
<script>
  import lineChart from 'components/base/charts/lineChart.vue'
  import histogram from 'components/base/charts/histogram.vue'
  import chargeAmount from 'components/base/charts/chargeAmount.vue'
  import tranalysic from 'components/base/charts/tranalysic.vue'
  import lineArea from 'components/base/charts/lineArea.vue'
  import { getParkingIncome, getValueRecharge, trafficNumber, getTransactionMonth, getParklotRank, getRegistrationCount, getTotalRecharge, getParkingActive, getParkingOrderForPage } from '@/api/home'
  import { toThousandslsFilter } from '@/utils/filters'
export default {
    name: 'home',
    data () {
      return {
        orderData: {
          todayorderIncome: '0',
          monthorderIncome: '0',
          chartData: []
        },
        tradeData: {
          todaytradeIncome: '0',
          monthtradeIncome: '0',
          chartData: []
        },
        chargeData: {
          todaychargeIncome: '0',
          monthchargeIncome: '0',
          chartData: []
        },
        trafficData: {
          todayTransportAttract: '0',
          spaceCount: '0',
          parklotCount: '0',
          chartData: []
        },
        monthTradeChartData: [],
        monthTradeSetting: {},
        getParklotIncomeRank: [],
        register: {
          monthInviteAmount: '0',
          monthRegisterAmount: '0',
          todayRegisterAmount: '0',
          userRegisterAmount: '0'
        },
        chargeTradeChart: [],
        chargeTradeSetting: {},
        mpActiveAmount: '0',
        ypActiveAmount: '0',
        tradeOrderData: {}
      }
    },
    components: { lineChart, histogram, chargeAmount, tranalysic, lineArea },
    filters: {toThousandslsFilter},
    created () {
      this.getParkingData()
      this.getRechargeData()
      this.getTransportAttract()
      this.getMonthTradeData()
      this.getParklotOrderRange()
      this.getRegistCount()
      this.getchargeTradeAnalysic()
      this.getParkingActiveData()
      this.getTradeOrderData()
    },
    methods: {
    // 
      getParkingData () {
        return new Promise((resolve, reject) => {
          getParkingIncome().then(res => {
            const data = res.returnObject
            this.orderData.todayorderIncome = data.todayOrderIncome
            this.orderData.monthorderIncome = data.monthOrderIncome
            this.tradeData.todaytradeIncome = data.todayTradeIncome[0]
            this.tradeData.monthtradeIncome = data.monthTradeIncome[0]
            let list = []
            let key
            for (key in data.data) {
              var obj = {
                name: key,
                value: [key, data.data[key].toFixed(2)]
              }
              list.push(obj)
            }
            list.push(obj)
            data.data = list
            this.orderData.chartData = data.ParkingOrderdate
            this.tradeData.chartData = list
            resolve(data)
          }, err => reject(err))
        })
      },
      // 
      getRechargeData () {
        return new Promise((resolve, reject) => {
          getValueRecharge().then(res => {
            const data = res.returnObject
            let list = []
            let key
            for (key in data.data) {
              var obj = {
                name: key,
                value: [key, parseInt(data.data[key]).toFixed(2)]
              }
              list.push(obj)
            }
            list.push(obj)
            data.data = list
            this.chargeData.chartData = list
            this.chargeData.todaychargeIncome = data.todayChargeCount
            this.chargeData.monthchargeIncome = data.monthChargeCount
            resolve(data)
          }, err => reject(err))
        })
      },
      // 
      getTransportAttract () {
        return new Promise((resolve, reject) => {
          trafficNumber().then(res => {
            const data = res.returnObject
            this.trafficData.todayTransportAttract = data.todayTransportAttract[0]
            this.trafficData.parklotCount = data.parklotCount
            this.trafficData.spaceCount = data.spaceCount
            this.trafficData.chartData = data.data
          })
        })
      },
      // 
      getMonthTradeData () {
        return new Promise((resolve, reject) => {
          getTransactionMonth().then(res => {
            const data = res.returnObject
            let y = []
            Object.keys(data).forEach(v => {
              this.monthTradeChartData.push(data[v])
              v.replace(/\//g, '-')
              y.push(v + '')
            })
            this.monthTradeSetting = {
              xAxis: [
                {
                  data: y
                }
              ]
            }
            resolve()
          }, err => reject(err))
        })
      },
      // 
      getParklotOrderRange () {
        return new Promise((resolve, reject) => {
          getParklotRank().then(res => {
            const data = res.returnObject
            this.getParklotIncomeRank = data.slice(0, 7)
            resolve()
          }, err => reject(err))
        })
      },
      // 
      getRegistCount () {
        return new Promise((resolve, reject) => {
          getRegistrationCount().then(res => {
            const data = res.returnObject
            this.register.monthInviteAmount = data.monthInviteAmount
            this.register.monthRegisterAmount = data.monthRegisterAmount
            this.register.todayRegisterAmount = data.todayRegisterAmount
            this.register.userRegisterAmount = data.userRegisterAmount
            resolve()
          }, err => reject(err))
        })
      },
      // 
      getchargeTradeAnalysic () {
        return new Promise((resolve, reject) => {
          getTotalRecharge().then(res => {
            const data = res.returnObject
            this.chargeTradeChart = data.data
            this.chargeTradeSetting = {
              title: {
                text: ' ' + toThousandslsFilter(data.total.value),
                textStyle: {
                  fontWeight: 'normal',
                  fontSize: 16
                }
              }
            }
            resolve()
          }, err => reject(err))
        })
      },
      // 
      getParkingActiveData () {
        return new Promise((resolve, reject) => {
          getParkingActive().then(res => {
            const data = res.returnObject
            this.mpActiveAmount = data.mptotal
            this.ypActiveAmount = data.yptotal
          })
        })
      },
      // 
      getTradeOrderData () {
        return new Promise((resolve, reject) => {
          getParkingOrderForPage().then(res => {
            const data = res.returnObject
            this.tradeOrderData = data
            debugger
          })
        })
      }
    }
  }
</script>




