<template>
  <div :class="className" :id="id" :style="{height:height,width:width}"></div>
</template>
<script>
// 
let echarts = require('echarts/lib/echarts')
// 
require('echarts/lib/chart/pie')
// title
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
require('echarts/lib/component/legend')
export default {
  name: 'chargeTradeAnalysic',
  props: {
    className: {
      type: String,
      default: 'echart'
    },
    id: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '200px'
    },
    height: {
      type: String,
      default: '200px'
    },
    orginData: {
      type: Array,
      default: _ => []
    },
    chartSetting: {
      type: Object,
      default: _ => {}
    }
  },
  data () {
    return {
      chart: null
    }
  },
  mounted () {
    window.addEventListener('resize', function () {
      this.chart.resize()
    })
  },
  beforeDestroy () {
    if (!this.chart) {
      return
    }
    this.chart.dispose()
    this.chart = null
  },
  watch: {
    orginData () {
      this.initChart()
    }
  },
  methods: {
    initChart () {
      this.chart = echarts.init(document.getElementById(this.id))
      const colorList = [
        '#11c47e', '#f2d143', '#3853ad', '#4899e3'
      ]
      echarts.util.each(this.orginData, function (item, index) {
        item.itemStyle = {
          normal: {
            color: colorList[index]
          }
        }
      })
      const originalData = [{
        value: 69489,
        name: ''

      }, {
        value: 69266,
        name: ''

      }, {
        value: 69456,
        name: ''

      }]
      let total = {
        value: '',
        name: ''
      }
      let option = {
        title: {
          text: ' ' + total.value,
          x: '100',
          y: '10',
          textStyle: {
            fontWeight: 'normal',
            fontSize: 16
          }
        },
        legend: {
          data: originalData,
          textStyle: {
            fontSize: 16
          },
          orient: 'vertical',
          top: '26%',
          right: '25%',
          padding: 30
        },
        tooltip: {
          trigger: 'item',
          formatter: '{b}: {c} ({d}%)'
        },
        backgroundColor: '#ffffff',
        series: [{
          radius: '65%',
          name: 'pie',
          type: 'pie',
          center: [210, 150],
          label: {
            normal: {
              show: false
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          itemStyle: {
            normal: {
              borderWidth: 3,
              borderColor: '#ffffff'
            }
            // emphasis: {
            //     borderWidth: 0,
            //     shadowBlur: 5,
            //     shadowOffsetX: 0,
            //     shadowColor: 'rgba(0, 0, 0, 0.2)'
            // }
          },
          data: this.orginData
        }]
      }
      const opt = Object.assign({}, option, this.chartSetting)
      this.chart.setOption(opt)
    }
  }
}
</script>


