<template>
  <div :class="className" :id="id" :style="{height:height,width:width}"></div>
</template>
<script>
// 
let echarts = require('echarts/lib/echarts')
// 
require('echarts/lib/chart/line')
// title
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')

export default {
  name: 'sl-line',
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    id: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '200px'
    },
    height: {
      type: String,
      default: '200px'
    },
    orginData: {
      type: Array,
      default: _ => []
    }
  },
  data () {
    return {
      chart: null
    }
  },
  mounted () {
    window.addEventListener('resize', function () {
      this.chart.resize()
    })
  },
  beforeDestroy () {
    if (!this.chart) {
      return
    }
    this.chart.dispose()
    this.chart = null
  },
  watch: {
    orginData () {
      this.initChart()
    }
  },
  methods: {
    initChart () {
      this.chart = echarts.init(document.getElementById(this.id))
      let option = {
        calculable: false,
        tooltip: {
          trigger: 'axis',
          formatter: function (params) {
            params = params[0]
            var date = new Date(params.name)
            return (date.getHours() > 12 ? 'PM ' + date.getHours() + ':00' : 'AM ' + date.getHours() + ':00') + ' ' + params.value[1]
          },
          axisPointer: {
            animation: false
          }
        },
        grid: {
          left: '-40',
          right: '0',
          bottom: '-30',
          height: '44px',
          containLabel: true
        },
        xAxis: [
          {
            type: 'time',
            boundaryGap: false,
            show: false
          }
        ],
        yAxis: [
          {
            type: 'value',
            show: false,
            splitLine: {
              show: false
            }

          }
        ],
        series: [
          {
            name: '',
            type: 'line',
            symbol: 'none', // 
            smooth: true, // 
            symbolSize: 3,
            areaStyle: {
              normal: {
                color: '#0098fe'
              }
            },
            data: this.orginData
          }
        ],
        color: ['#0098fe']
      }
      this.chart.setOption(option)
    }
  }

}
</script>


